.divButton {
  justify-content: center;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.saveButton {
  height: 36px;
  font-size: 12px;
  line-height: 12px;
  font-weight: 100;
  padding: 11px 24px;
  color: #ffffff;
  background-color: #008662;
  border: none;
  border-radius: 4px;
  /* margin-right: 25px; */
}

.saveButton:disabled {
  background-color: #b1b1b1 !important;
  cursor: not-allowed;
}

.generateButton {
  height: 36px;
  font-size: 12px;
  line-height: 12px;
  font-weight: 100;
  padding: 11px 24px;
  color: #ffffff;
  background-color: #b1b1b1;
  border: none;
  border-radius: 4px;
}

.divNoAvailableChannelSelected {
