  border-radius: 6px;
  color: #008662;
  font-weight: 300;
  font-size: 24px;
  text-align: center;
  background-color: #e8e8e8;
  padding-left: 25px;
  padding-right: 25px;
  display: table;
  width: 100%;
  margin-top: 23px;
}

.divNoAvailableChannelSelectedSmall {
  height: calc(100vh - 565px);
  border-radius: 6px;
  color: #008662;
  font-weight: 300;
  font-size: 24px;
  text-align: center;
  background-color: #e8e8e8;
  padding-left: 25px;
  padding-right: 25px;
  display: table;
  width: 100%;
  margin-top: 25px;
}

.importButton {
  background-color: #008662;
  font-size: 12px;
  font-weight: 100;
  padding: 7px 12px;
  height: 32px;
  border: none;
  border-radius: 4px;
  color: #ffffff;
  text-align: center;
  width: 120px;
  margin-left: 10px;
}

.cancelButton {
  background-color: #ffffff;
  font-size: 12px;
  font-weight: 100;
  padding: 7px 12px;
  height: 32px;
  border: none;
  border-radius: 4px;
  color: #5c636a;
  text-align: center;
  width: 120px;
  margin-left: 10px;
}

.successDiv {
  height: calc(100vh - 270px);
  margin-top: 10px;
  border: 1px solid #e2e8f0;
  overflow: auto;
}
.successBody {
  display: table;
  width: 100%;
  table-layout: fixed;
}

.trBorder {
  border: 1px solid #e2e8f0;
}

.formCheck:checked {
  background-color: #008662 !important;
  border-color: #008662 !important;
}



.saveModalButton {
  height: 36px;
  font-size: 12px;
  line-height: 12px;
  font-weight: 100;
  padding: 11px 24px;
  background-color: #008662;
  color: #ffffff;
  border: none;
  border-radius: 4px;
  width: 100%;
  margin-top: 20px;
}

.tableBodyDiv {
  overflow-x: auto;
  overflow-y: auto;
  height: calc(100vh - 468px);
}

.tableBodyDivSmall {
  overflow-x: auto;
  overflow-y: auto;
  height: calc(100vh - 684px);
}

.prioritizeTableBodyDiv {
  overflow-x: auto;
  overflow-y: auto;
  height: calc(100vh - 468px);
}

.prioritizeTableBodyDivSmall {
  overflow-x: auto;
  overflow-y: auto;
  height: calc(100vh - 684px);
}

.tableBodyDiv::-webkit-scrollbar-thumb,
.tableBodyDivSmall::-webkit-scrollbar-thumb,
.prioritizeTableBodyDiv::-webkit-scrollbar-thumb,
.prioritizeTableBodyDivSmall::-webkit-scrollbar-thumb {
  background-color: #cbcbcb;
  box-shadow: inset 0 0 0 3px #ffffff;
  border-left: 1px solid #dee3eb;
  border-right: 1px solid #dee3eb;
}

.tableBodyDiv::-webkit-scrollbar-track,
.tableBodyDivSmall::-webkit-scrollbar-track,
.prioritizeTableBodyDiv::-webkit-scrollbar-track,
.prioritizeTableBodyDivSmall::-webkit-scrollbar-track {
  border-right: 1px solid #dee3eb;
  border-left: 1px solid #dee3eb;
}

.tableBodyDiv::-webkit-scrollbar,
.tableBodyDivSmall::-webkit-scrollbar,
.prioritizeTableBodyDiv::-webkit-scrollbar,
.prioritizeTableBodyDivSmall::-webkit-scrollbar {
  background-color: #ffffff;
  width: 14px;
  padding: 5px;
}

.divButtons {
  min-width: 102px;
  max-width: 102px;
  margin-left: 6px;
  margin-right: 6px;
  justify-content: center;
  display: flex;
  flex-direction: column;
}

.divButtons button {
  display: flex;
  height: 38px;
  padding: 1px 5px 0px 5px;
  background-color: #008662;
  font-size: 12px;
  font-weight: 100;
  border-radius: 4px;
  border: 1px solid #008662;
  color: #ffffff;
}

.divButtons button + button {
  margin-top: 5px;
}

.divButtons button:active,
.divButtons button:hover {
  background-color: #116c53;
  border: 1px solid #116c53;
}

.divButtons button:focus {
  border: 1px solid #116c53;
  outline: none;
}

.divButtons button:disabled {
  background-color: #b1b1b1;
  border: 1px solid #b1b1b1;
  cursor: not-allowed;
}

.searchWrapper {
  display: flex;
  width: 100%;
  margin-bottom: 5px;
}

.search {
  height: 28px;
  width: 100%;
  border: 1px solid #b1b1b1;
  border-right: none;
  border-radius: 4px 0px 0px 4px;
  font-size: 12px;
  text-indent: 5px;
}

.search:focus {
  outline: none;
}

.timesIcon {
  content: url('../../assets/icons/times.svg');
  width: 12.7px;
  height: auto;
  margin-top: 6px;
  margin-left: 10px;
  cursor: pointer;
}

.searchIcon {
  content: url('../../assets/icons/magnifying-glass.svg');
  margin-top: 6px;
  margin-left: 5px;
  cursor: pointer;
}

.timesIcon:hover,
.searchIcon:hover {
  filter: brightness(0) saturate(100%) invert(30%) sepia(86%) saturate(1538%)
    hue-rotate(142deg) brightness(87%) contrast(101%);
}

.timesIconWrapper {
  width: 30px;
  background-color: #ffffff;
  border: 1px solid #b1b1b1;
  border-left: none;
  border-right: none;
}

.searchIconWrapper {
  width: 30px;
  background-color: #ffffff;
  border: 1px solid #b1b1b1;
  border-left: none;
  border-radius: 0px 4px 4px 0px;
}

.tableLabel {
  color: #6d6d73;
  font-size: 14px;
  font-weight: 400;
  margin-bottom: 3px;
}

.isHighlighted {
  border-right: 2px solid #008662;
}