.panel {
    background: #ffffff;
    box-shadow: 0px 15px 50px rgba(0, 0, 0, 0.15);
    border-radius: 5px;
    padding: 15px 17px;
    width: 100%;
    border: 1px solid #008662;
    height: 210px;
  }

  .scroll{
    height: 120px;
    overflow-y: auto;
  }
  
  .closeIcon {
    right: 0;
    width: 12.73px;
    height: 12.73px;
    position: absolute;
    filter: brightness(0) invert(0.7);
    cursor: pointer;
  }
  
  .fontBold {
    list-style-type: none;  
    font-weight: bolder;
    font-size: 14px;
    display: block;
  }
  .labelField {
    color: #b1b1b1;
    width: auto;
    font-weight: normal;
    font-size: 14px;
    display: block;
  }
  